table {
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  font-size: 14px;
  border-collapse: collapse;
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
  width: 760px;
}

th,
td:first-child {
  background: #AFCDE7;
  color: white;
  padding: 10px 20px;
}

th,
td {
  border-style: solid;
  border-width: 0 1px 1px 0;
  border-color: white;
}

td {
  background: #D8E6F3;
}

th:first-child,
td:first-child {
  text-align: left;
}

.table-row:hover {
cursor: pointer;
}